
/* footer */
.footer {
    height: 309px;
    background-color: #fafafa;
    text-align: center;
    h2 {
        padding-top: 50px;
    }
}
.footer_menu {
    width: 305px;
    height: 32px;
    margin: 20px auto;
    text-align: center;
    font-size: 0;
    border: 1px solid #ebebeb;
    border-width: 1px 0;
    color: #737373;
    a { 
        display: inline-block;
        padding: 5px;
        color: #737373;
        font-size:13px;
    }
}
address {
   overflow:hidden;
    line-height:23px;
    margin:0 auto;
    font-style: normal;
    font-size:12px;
    color: #8c8c8c;
    a {
        font-size:12px;
        font-weight:bold;
        padding:0 10px;
        color: #737373;
    }
}
.copyright {
    margin-top: 20px;
    font-size: 11px;
    color: #8c8c8c;
    font-weight:bold;
}